import React from 'react';
import axios from 'axios';
const API_PATH = '/api/index.php';

class Shuffle extends React.Component{
    constructor(){
        super()
        this.state = {
            data: []
        }
    }

    componentDidMount(){
        axios({
            method: 'post',
            url: `${API_PATH}`,
            headers: { 'content-type': 'application/json' },
            data: { player : this.props.numberOfPlayer }
          })
            .then(result => {
              console.log(typeof(result));
              this.setState({
                  data: result
              })
              console.log(this.state.data);
            })
            .catch(error => console.log(error) );
    }

    render(){
        return(
            <div>
            <h1>{this.props.numberOfPlayer}</h1>
            {this.state.data.map((getData) => (
                <div key = {getData.id} className="card">
                    <p>{getData.data}</p>
                </div>
                ))
            }
            </div>
        )
    }
}

export default Shuffle